	module.exports = {"pathContext":{"docHtml":"<h1>streams WG Meeting 2015-01-30</h1>\n<h2>Links</h2>\n<ul>\n<li><strong>Google Hangouts Video</strong>: <a href=\"http://www.youtube.com/watch?v=I9nDOSGfwZg\">http://www.youtube.com/watch?v=I9nDOSGfwZg</a></li>\n<li><strong>GitHub Issue</strong>: <a href=\"https://github.com/iojs/readable-stream/issues/106\">https://github.com/iojs/readable-stream/issues/106</a></li>\n<li><strong>Original Minutes Google Doc</strong>: <a href=\"https://docs.google.com/document/d/17aTgLnjMXIrfjgNaTUnHQO7m3xgzHR2VXBTmi03Qii4/\">https://docs.google.com/document/d/17aTgLnjMXIrfjgNaTUnHQO7m3xgzHR2VXBTmi03Qii4/</a></li>\n</ul>\n<h2>Agenda</h2>\n<p>Extracted from <a href=\"https://github.com/iojs/readable-stream/labels/wg-agenda\">https://github.com/iojs/readable-stream/labels/wg-agenda</a> prior to meeting.</p>\n<ul>\n<li>adopt a charter <a href=\"https://github.com/iojs/readable-stream/issues/105\">#105</a></li>\n<li>release and versioning strategy <a href=\"https://github.com/iojs/readable-stream/issues/101\">#101</a></li>\n<li>simpler stream creation <a href=\"https://github.com/iojs/readable-stream/issues/102\">#102</a></li>\n<li>proposal: deprecate implicit flowing of streams <a href=\"https://github.com/iojs/readable-stream/issues/99\">#99</a></li>\n</ul>\n<h2>Minutes</h2>\n<h3>adopt a charter</h3>\n<ul>\n<li>group: +1's all around</li>\n</ul>\n<h3>What versioning scheme should be adopted?</h3>\n<ul>\n<li>group: +1’s 3.0.0</li>\n<li>domenic+group: pulling in patches from other sources where appropriate</li>\n<li>mikeal: version independently, suggesting versions for io.js</li>\n<li>mikeal+domenic: work with TC to notify in advance of changes\nsimpler stream creation</li>\n</ul>\n<h3>streamline creation of streams</h3>\n<ul>\n<li>\n<p>sam: streamline creation of streams</p>\n</li>\n<li>\n<p>domenic: nice simple solution posted\nbut, we lose the opportunity to change the model\nmay not be backwards incompatible (double check keys)</p>\n<p><strong>action item:</strong> domenic will check</p>\n</li>\n</ul>\n<h3>remove implicit flowing of streams on(‘data’)</h3>\n<ul>\n<li>add isFlowing / isPaused</li>\n<li>mikeal: worrying that we’re documenting polyfill methods – confuses users</li>\n<li>domenic: more reflective API is probably good, with warning labels for users</li>\n<li>new section for mad scientists (reflective stream access)</li>\n<li>calvin: name the “third state”</li>\n<li>mikeal: maybe borrow the name from whatwg?</li>\n<li>domenic: we’re missing the “third state”</li>\n<li>consensus: kind of difficult to name the third state</li>\n<li>mikeal: figure out differences in states / compat</li>\n<li>\n<p>mathias: always flow on data – eliminates third state</p>\n<ul>\n<li>explore what it breaks</li>\n</ul>\n</li>\n</ul>\n<p><strong>action items:</strong></p>\n<ul>\n<li>ask isaac for ability to list packages by what public io.js APIs they use (esp. Stream)</li>\n<li>ask rod/build for infrastructure</li>\n<li><strong>chris</strong>: explore the “flow on data” approach</li>\n<li>add isPaused/isFlowing</li>\n<li>add new docs section</li>\n<li>move isPaused to that section</li>\n</ul>","docPath":"argo-ci/node_modules/github-webhook-handler/node_modules/readable-stream/doc/wg-meetings/2015-01-30","proj":"argo-ci"}}

